import React, { useEffect, useRef, useState } from 'react';
import { babylonEngineManager } from '../babylon/BabylonEngineManager';
import { EnergyBoxFeature } from '../babylon/features/EnergyBoxFeature';
import { EffectsDrivenParticlesFeature } from '../babylon/features/EffectsDrivenParticlesFeature';
import { OutletFeature } from '../babylon/features/OutletFeature';
import { DistortionShockFeature } from '../babylon/features/DistortionShockFeature';
import { DuelOpponentFeature } from '../babylon/features/DuelOpponentFeature';

interface AdvStatus { mode: 'loading'|'webgpu'|'webgl'|'error'; message: string; frames: number; }

// More modular Babylon stage using BabylonEngineManager so we can add features (particles, power-ups, UI overlays) later
export const BabylonStageAdvanced: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const [status, setStatus] = useState<AdvStatus>({ mode: 'loading', message: 'Initializing...', frames: 0 });
  const [fps, setFps] = useState(0);
  const lastFpsT = useRef(performance.now());
  const frameCounter = useRef(0);
  const frameRef = useRef(0);

  useEffect(() => {
    let disposed = false;
    (async () => {
      if (!canvasRef.current) return;
      try {
        const B = await import('@babylonjs/core');
        await import('@babylonjs/loaders');
        const canvas = canvasRef.current;
        let engine: any = null;
        if ((navigator as any).gpu && B.WebGPUEngine) {
          try { const webgpu = new B.WebGPUEngine(canvas, { adaptToDeviceRatio: true }); await webgpu.initAsync(); engine = webgpu; setStatus(s=>({ ...s, mode:'webgpu', message:'Babylon WebGPU'})); } catch (e) { console.warn('WebGPU init failed', e); }
        }
        if (!engine) { engine = new B.Engine(canvas, true); setStatus(s=>({ ...s, mode:'webgl', message:'Babylon WebGL'})); }
        const scene = new B.Scene(engine);
        const camera = new B.ArcRotateCamera('cam', Math.PI/4, Math.PI/3, 6, B.Vector3.Zero(), scene); camera.attachControl(canvas, true);
        new B.HemisphericLight('hl', new B.Vector3(0,1,0), scene);
        babylonEngineManager.init(engine, scene, canvas);
        // Register core features
        babylonEngineManager.registerFeature(EnergyBoxFeature);
        babylonEngineManager.registerFeature(OutletFeature);
        babylonEngineManager.registerFeature(EffectsDrivenParticlesFeature);
        babylonEngineManager.registerFeature(DistortionShockFeature);
  babylonEngineManager.registerFeature(DuelOpponentFeature);
        engine.runRenderLoop(() => { 
          scene.render(); 
          frameRef.current++; frameCounter.current++;
          const now = performance.now();
          if (now - lastFpsT.current > 1000) {
            setFps(Math.round(frameCounter.current * 1000 / (now - lastFpsT.current)));
            frameCounter.current = 0; lastFpsT.current = now;
          }
          if (frameRef.current % 60 === 0) setStatus(s=>({ ...s, frames: frameRef.current }));
        });
        const resize = () => engine.resize(); window.addEventListener('resize', resize);
        if (disposed) return;
        return () => { disposed = true; window.removeEventListener('resize', resize); babylonEngineManager.dispose(); };
      } catch (e:any) {
        setStatus({ mode:'error', message: e?.message || 'Init error', frames: 0 });
      }
    })();
  }, []);

  return (
    <div style={{border:'1px solid #334', padding:8, borderRadius:8, background:'#0d0f15', color:'#eaeaea'}}>
  <div style={{fontSize:12, marginBottom:6}}>Mod Babylon: {status.message} frames:{status.frames} fps:{fps}</div>
      <canvas ref={canvasRef} style={{width:'100%', height:300, display:'block', background:'#050608'}} />
    </div>
  );
};

export default BabylonStageAdvanced;
