import { Color3, MeshBuilder, StandardMaterial, Vector3 } from '@babylonjs/core';
import type { BabylonFeatureInit } from '../BabylonEngineManager';
import { effectsBus } from '../../effects/effectsBus';

export const OutletFeature: BabylonFeatureInit = ({ scene }) => {
  const base = MeshBuilder.CreateBox('outletBase', { width: 2.2, height: 3.0, depth: 0.25 }, scene);
  base.position = new Vector3(0,0,0);
  const mat = new StandardMaterial('outletMat', scene);
  mat.diffuseColor = new Color3(0.12,0.12,0.14);
  mat.specularColor = new Color3(0.05,0.05,0.05);
  base.material = mat;
  const pulse = { t: 0 };
  const obs = scene.onBeforeRenderObservable.add(() => {
    if (pulse.t > 0) {
      pulse.t -= scene.getEngine().getDeltaTime()*0.004;
      const f = Math.max(0, pulse.t);
      mat.emissiveColor = new Color3(0.8+0.2*f, 0.45+0.4*f, 0.15+0.2*f);
    } else {
      mat.emissiveColor = new Color3(0.8,0.45,0.15);
    }
  });
  const unsubClick = effectsBus.on('outletClick', () => { pulse.t = 1; });
  const unsubShock = effectsBus.on('shock', ({ severity }) => { pulse.t = severity==='critical'? 2.2 : severity==='severe'?1.6:1.1; });
  return () => { unsubClick(); unsubShock(); scene.onBeforeRenderObservable.remove(obs); try { base.dispose(); } catch {}; };
};
