import { Color4, ParticleSystem, Texture, Vector3 } from '@babylonjs/core';
import type { BabylonFeatureInit } from '../BabylonEngineManager';
import { effectsBus } from '../../effects/effectsBus';

// Creates a pooled particle system attached near origin and triggers bursts on effect events.
export const EffectsDrivenParticlesFeature: BabylonFeatureInit = ({ scene }) => {
  const texData = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR4nGMAAQAABQABDQottAAAAABJRU5ErkJggg==';
  const ps = new ParticleSystem('fxParticles', 4000, scene);
  ps.particleTexture = new Texture(texData, scene, true, false, Texture.NEAREST_SAMPLINGMODE);
  ps.emitter = new Vector3(0,0,0);
  ps.minEmitPower = 0.3; ps.maxEmitPower = 2.2;
  ps.minSize = 0.05; ps.maxSize = 0.35;
  ps.minLifeTime = 0.3; ps.maxLifeTime = 1.2;
  ps.emitRate = 0; // we'll trigger manual bursts
  ps.color1 = new Color4(1,0.65,0.25,1);
  ps.color2 = new Color4(1,0.35,0.1,1);
  ps.colorDead = new Color4(0.2,0.1,0.05,0);
  ps.updateSpeed = 0.012;
  ps.start();

  function burst(intensity: number) {
    // Temporarily raise emitRate for a frame
    const original = ps.emitRate;
    ps.emitRate = intensity;
    setTimeout(()=>{ ps.emitRate = original; }, 60);
  }

  const unsubLevel = effectsBus.on('levelUp', ({ level }) => burst(400 + level*40));
  const unsubShock = effectsBus.on('shock', ({ severity }) => {
    const mult = severity==='critical'? 600: severity==='severe'? 420: severity==='moderate'? 300: 180;
    burst(mult);
  });
  const unsubStage = effectsBus.on('stageAdvance', ({ stage }) => burst(500 + stage*60));
  const unsubDrop = effectsBus.on('itemDrop', ({ level }) => burst(260 + level*30));
  const unsubAi = effectsBus.on('aiAttack', ({ voltage }) => burst(300 + Math.min(600, voltage)));

  return () => {
    unsubLevel(); unsubShock(); unsubStage(); unsubDrop(); unsubAi();
    try { ps.dispose(); } catch {}
  };
};
