import { PostProcess } from '@babylonjs/core';
import type { BabylonFeatureInit } from '../BabylonEngineManager';
import { effectsBus } from '../../effects/effectsBus';

// Simple fullscreen fragment that warps UVs based on a time-varying intensity.
export const DistortionShockFeature: BabylonFeatureInit = ({ scene }) => {
  let intensity = 0;
  const pp = new PostProcess('shockDistortion', `
    precision highp float;
    varying vec2 vUV;
    uniform float iTime; uniform float iIntensity;
    float hash(vec2 p){return fract(sin(dot(p,vec2(41.3,289.1)))*43758.5453);} 
    void main(){
      vec2 uv=vUV; 
      float n = hash(floor(uv*vec2(64.0))+floor(iTime*10.0));
      uv += (n-0.5)*0.02*iIntensity;
      gl_FragColor = texture2D(textureSampler, uv);
    }
  `, ['iTime','iIntensity'], null, 1.0, null, undefined, scene.getEngine(), true);
  let start = performance.now();
  pp.onApply = (eff) => {
    const t = (performance.now()-start)/1000;
    intensity = Math.max(0, intensity - scene.getEngine().getDeltaTime()*0.0006);
    eff.setFloat('iTime', t);
    eff.setFloat('iIntensity', intensity);
  };
  const unsubShock = effectsBus.on('shock', ({ severity }) => {
    intensity = severity==='critical'?1.6: severity==='severe'?1.2: severity==='moderate'?0.8:0.5;
  });
  return () => { unsubShock(); try { pp.dispose(); } catch {}; };
};
