import { MeshBuilder, StandardMaterial, Color3, Vector3 } from '@babylonjs/core';
import type { BabylonFeatureInit } from '../BabylonEngineManager';
import { effectsBus } from '../../effects/effectsBus';

export const DuelOpponentFeature: BabylonFeatureInit = ({ scene }) => {
  const bar = MeshBuilder.CreateBox('opponentBar', { width: 0.3, height: 0.1, depth: 0.3 }, scene);
  bar.position = new Vector3(1.6, 1.2, 0);
  const mat = new StandardMaterial('opponentMat', scene); mat.emissiveColor = new Color3(0.2,0.5,1); bar.material = mat;
  const unsub = effectsBus.on('duelState', ({ players }) => {
    // Take first opponent (not local later we could filter) – scale height by score fraction heuristic.
    const list = Object.values(players);
    if (!list.length) return;
    const maxScore = list.reduce((m,p)=> p.score>m? p.score:m, 1);
    const p = list[0];
    const h = 0.3 + Math.min(2.5, (p.score / (maxScore||1)) * 1.5);
    bar.scaling.y = h;
    bar.position.y = 0.5 + h/2;
    mat.emissiveColor = new Color3(0.2 + (p.streak*0.05)%0.5, 0.5, 1 - Math.min(0.7, p.volts/1000));
  });
  return () => { unsub(); try { bar.dispose(); } catch {}; };
};
