import { Color3, GlowLayer, MeshBuilder, StandardMaterial } from '@babylonjs/core';
import type { BabylonFeatureInit } from '../BabylonEngineManager';

// Simple rotating emissive energy cube with glow
export const EnergyBoxFeature: BabylonFeatureInit = ({ scene }) => {
  const box = MeshBuilder.CreateBox('energyBox', { size: 1.2 }, scene);
  const mat = new StandardMaterial('energyMat', scene);
  mat.emissiveColor = new Color3(1, 0.5, 0.15);
  box.material = mat;
  let glow: GlowLayer | null = null;
  try { glow = new GlowLayer('glow', scene, { blurKernelSize: 32 }); glow.intensity = 0.6; glow.addIncludedOnlyMesh(box); } catch {}
  const obs = scene.onBeforeRenderObservable.add(() => {
    box.rotation.y += 0.01; box.rotation.x += 0.005;
  });
  return () => { scene.onBeforeRenderObservable.remove(obs); try { box.dispose(); } catch {}; try { glow?.dispose(); } catch {} };
};
