// Centralized Babylon.js engine & scene manager to allow modular feature registration
import type { Engine, Scene } from '@babylonjs/core';

export type BabylonFeatureInit = (ctx: { engine: Engine; scene: Scene; canvas: HTMLCanvasElement }) => void | (() => void);

class BabylonEngineManager {
  private static _i: BabylonEngineManager;
  static get instance() { return this._i || (this._i = new BabylonEngineManager()); }

  engine: Engine | null = null;
  scene: Scene | null = null;
  private features: BabylonFeatureInit[] = [];
  private disposers: (() => void)[] = [];
  private ready = false;

  registerFeature(init: BabylonFeatureInit) {
    if (this.ready && this.engine && this.scene && this.canvas) {
      const d = init({ engine: this.engine, scene: this.scene, canvas: this.canvas });
      if (typeof d === 'function') this.disposers.push(d);
    } else {
      this.features.push(init);
    }
  }

  canvas: HTMLCanvasElement | null = null;

  init(engine: Engine, scene: Scene, canvas: HTMLCanvasElement) {
    this.engine = engine; this.scene = scene; this.canvas = canvas; this.ready = true;
    // flush queued features
    for (const f of this.features) {
      const d = f({ engine, scene, canvas });
      if (typeof d === 'function') this.disposers.push(d);
    }
    this.features = [];
  }

  dispose() {
    for (const d of this.disposers) { try { d(); } catch { /* ignore */ } }
    this.disposers = [];
    try { this.scene?.dispose(); } catch {}
    try { this.engine?.dispose(); } catch {}
    this.engine = null; this.scene = null; this.ready = false; this.canvas = null;
  }
}

export const babylonEngineManager = BabylonEngineManager.instance;
